import React, { Fragment } from 'react'
import MetaTags from 'react-meta-tags'
import LayoutOne from '../layouts'
import HeroSliderTwo from '../containers/hero-sliders/hero-slider-two'
import ServiceTwo from '../containers/services/service-two'
import ServiceThree from '../containers/services/service-three'
import ContactFaqOne from '../containers/contact-faqs/contact-faq-one'

const HomeTwo = () => {
    return (
        <Fragment>
            <MetaTags>
                <title>Ammbr - Blockchain-based wireless mesh network</title>
                <meta
                    name="description"
                    content="Ammbr is a blockchain-based wireless mesh network that buys and sells Internet bandwidth autonomously, using the AMR cryptocurrency."
                />
            </MetaTags>
            <LayoutOne footerBg="white">
                {/* hero slider */}
                <HeroSliderTwo />
                {/* service */}
                <ServiceThree />
                {/* service with text */}
                <ServiceTwo />
                {/* contact faq */}
                <ContactFaqOne />
            </LayoutOne>
        </Fragment>
    )
}

export default HomeTwo
