import React from 'react'
import { Link } from 'react-router-dom'
import logo from '../../assets/images/logo/ammbr-logo@0.08x.png'

const Logo = () => {
    return (
        <div className="logo">
            <Link to={process.env.PUBLIC_URL + '/'}>
                <img src={logo} alt="Ammbr" className="img-fluid" />
            </Link>
        </div>
    )
}

export default Logo
